.navbar {
display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #013914;
    color: white;
position: sticky;
    top: 0;
    z-index: 999;
transition: 0.5s all ease;

&.active {
        background-color: white;
        color: black;
button {
        background-color: white !important;
        color: #1dbf73 !important;
        border: 1px solid #1dbf73 !important;
    }
  }

  .container {
      width: 1200px;
        display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0px;

    .logo {
            font-size: 34px;
            font-weight: bold;

    .text {}

                  .dot {
        font-weight: bold;
        color: #1dbf73;
        }
                }
        .links {
            display: flex;
    align-items: center;
    gap: 24px;
        font-family: "Montserrat", sans-serif;
    font-weight: 500;
span {
        white-space: nowrap;
    }

                  .user {
                      display: flex;
                      align-items: center;
                      gap: 10px;
                      cursor: pointer;
                      position: relative;

        img {
                width: 32px;
                height: 32px;
                border-radius: 50%;
                object-fit: cover;
            }

                                .options {
                                    position: absolute;
                                    top: 50px;
                                    right: 0;
                                    padding: 20px;
        background-color: white;
            border-radius: 10px;
        z-index: 999;
            border: 1px solid lightgrey;
        display: flex;
            flex-direction: column;
            gap: 10px;
        width: 200px;
            font-weight: 300;
        color: gray;
        }
        }

                  button {
        color: white;
            padding: 10px 20px;
            border-radius: 5px;
            border: 1px solid white;
        background-color: transparent;
        cursor: pointer;

        &:hover {
                background-color: #1dbf73;
                border-color: #1dbf73;
            }
        }
        }
                }

  hr {
    width: calc(100% - 2px);
        height: 0;
    border-top: 0.5px solid #ebe9e9;
        border-bottom: 0.5px solid #ebe9e9;
    }

  .menu {
      width: 1200px;
        padding: 10px 0px;
        display: flex;
        justify-content: space-between;
    color: gray;
    font-weight: 300;
    font-family: "Montserrat", sans-serif;
    }
}
