.categorySlide {
    width: 250px;
    height: 350px;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;

    }

    .desc {
        font-weight: 250;
        position: absolute;
        top: 15px;
        left: 15px;

    }

    .title {
        font-weight: 500;
        font-size: 24px;
        position: absolute;
        left: 15px;
        top: 40px;
    }
}